import React, { useState, useEffect, useRef, memo } from 'react';
import { Droplet, AlertTriangle, Heart, Leaf, Factory, Trash2, Users, ChevronLeft, ChevronRight, ZoomIn, ZoomOut, Maximize2 } from 'lucide-react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const pollutionImages = [
    {
      url: 'https://images.unsplash.com/photo-1621451537084-482c73073a0f?w=1920&h=600&fit=crop&q=80',
      title: 'Industrial Waste Pollution',
      description: 'Factories dumping toxic chemicals into rivers'
    },
    {
      url: 'https://images.unsplash.com/photo-1605600659908-0ef719419d41?w=1920&h=600&fit=crop&q=80',
      title: 'Plastic Pollution in Oceans',
      description: 'Marine life threatened by plastic waste'
    },
    {
      url: 'https://images.unsplash.com/photo-1530587191325-3db32d826c18?w=1920&h=600&fit=crop&q=80',
      title: 'River Pollution',
      description: 'Contaminated water bodies affecting communities'
    },
    {
      url: 'https://images.unsplash.com/photo-1583475092727-5a07f10eee25?w=1920&h=600&fit=crop&q=80',
      title: 'Urban Sewage Crisis',
      description: 'Untreated wastewater in city waterways'
    },
    {
      url: 'https://images.unsplash.com/photo-1618477461853-cf6ed80faba5?w=1920&h=600&fit=crop&q=80',
      title: 'Oil Spills & Marine Damage',
      description: 'Devastating impact on aquatic ecosystems'
    }
  ];

const fullPollutionData = [
    { year: 2000, unsafeWater: 39, plasticOcean: 4.8, diarrheaDeaths: 1300 },
    { year: 2001, unsafeWater: 38.5, plasticOcean: 5.0, diarrheaDeaths: 1280 },
    { year: 2002, unsafeWater: 38, plasticOcean: 5.1, diarrheaDeaths: 1260 },
    { year: 2003, unsafeWater: 37.5, plasticOcean: 5.2, diarrheaDeaths: 1240 },
    { year: 2004, unsafeWater: 37, plasticOcean: 5.3, diarrheaDeaths: 1220 },
    { year: 2005, unsafeWater: 37, plasticOcean: 5.2, diarrheaDeaths: 1200 },
    { year: 2006, unsafeWater: 36, plasticOcean: 5.5, diarrheaDeaths: 1180 },
    { year: 2007, unsafeWater: 35.5, plasticOcean: 6.0, diarrheaDeaths: 1160 },
    { year: 2008, unsafeWater: 35, plasticOcean: 6.5, diarrheaDeaths: 1140 },
    { year: 2009, unsafeWater: 34.5, plasticOcean: 7.0, diarrheaDeaths: 1120 },
    { year: 2010, unsafeWater: 34, plasticOcean: 8.0, diarrheaDeaths: 1100 },
    { year: 2011, unsafeWater: 33, plasticOcean: 8.5, diarrheaDeaths: 1080 },
    { year: 2012, unsafeWater: 32.5, plasticOcean: 9.0, diarrheaDeaths: 1050 },
    { year: 2013, unsafeWater: 32, plasticOcean: 9.3, diarrheaDeaths: 1020 },
    { year: 2014, unsafeWater: 31.5, plasticOcean: 9.6, diarrheaDeaths: 1000 },
    { year: 2015, unsafeWater: 31, plasticOcean: 10.0, diarrheaDeaths: 950 },
    { year: 2016, unsafeWater: 30, plasticOcean: 10.5, diarrheaDeaths: 920 },
    { year: 2017, unsafeWater: 29.5, plasticOcean: 11.0, diarrheaDeaths: 900 },
    { year: 2018, unsafeWater: 29, plasticOcean: 11.5, diarrheaDeaths: 880 },
    { year: 2019, unsafeWater: 28.5, plasticOcean: 12.0, diarrheaDeaths: 1100 },
    { year: 2020, unsafeWater: 28, plasticOcean: 1.4, diarrheaDeaths: 1100 },
    { year: 2021, unsafeWater: 27.5, plasticOcean: 1.45, diarrheaDeaths: 1090 },
    { year: 2022, unsafeWater: 27, plasticOcean: 1.5, diarrheaDeaths: 1100 },
    { year: 2023, unsafeWater: 26.5, plasticOcean: 1.5, diarrheaDeaths: 1080 },
    { year: 2024, unsafeWater: 26, plasticOcean: 1.6, diarrheaDeaths: 1050 }
  ];

const HomePage = memo(function HomePageComponent({ setActiveSection, zoomLevel, setZoomLevel }) {
    const [currentSlide, setCurrentSlide] = useState(0);
    const [chartData, setChartData] = useState([]);
    const [animationProgress, setAnimationProgress] = useState(0);
    const [chartDomain, setChartDomain] = useState({ start: 0, end: 0 });

    const maxIndex = fullPollutionData.length - 1;
    const slideTimerRef = useRef(null);
  
    useEffect(() => {
      setChartDomain({ start: 0, end: maxIndex });
    }, [maxIndex]);
  
    useEffect(() => {
      slideTimerRef.current = setInterval(() => {
        setCurrentSlide(prev => (prev + 1) % pollutionImages.length);
      }, 5000);
  
      return () => {
        if (slideTimerRef.current) {
          clearInterval(slideTimerRef.current);
          slideTimerRef.current = null;
        }
      };
    }, []);
  
    useEffect(() => {
      if (chartDomain.end > 0 && chartData.length < fullPollutionData.length) {
        const interval = setInterval(() => {
          setChartData((prev) => {
            const nextIndex = prev.length;
            if (nextIndex < fullPollutionData.length) {
              return [...prev, fullPollutionData[nextIndex]];
            }
            return prev;
          });
          setAnimationProgress((prev) => Math.min(prev + 1, fullPollutionData.length));
        }, 200);
        return () => clearInterval(interval);
      }
    }, [chartData.length, chartDomain.end]);
  
    useEffect(() => {
      if (chartDomain.end > 0) {
        const endIndex = Math.min(chartDomain.end, fullPollutionData.length - 1);
        const startIndex = chartDomain.start;
        
        if (chartData.length === 0 || 
            chartData[0]?.year !== fullPollutionData[startIndex]?.year ||
            chartData.length > (endIndex - startIndex + 1)) {
          setChartData([fullPollutionData[startIndex]]);
          setAnimationProgress(1);
        }
      }
    }, [chartDomain.start, chartDomain.end, chartData.length]);
  
    const prevSlide = () => {
        setCurrentSlide((prev) => (prev - 1 + pollutionImages.length) % pollutionImages.length);
    };
    const nextSlide = () => {
        setCurrentSlide(prev => (prev + 1) % pollutionImages.length);
    };
  
    const handleZoomIn = () => setZoomLevel(prev => Math.min(prev + 0.5, 3));
    const handleZoomOut = () => setZoomLevel(prev => Math.max(prev - 0.5, 1));
    const handleResetZoom = () => setZoomLevel(1);

    const newspaperCutouts = [
        { top: '5%', left: '2%', rotate: '8deg', delay: '0s', duration: '6s', img: 'https://images.unsplash.com/photo-1611273426858-450d8e3c9fce?w=500&h=400&fit=crop&q=80', title: 'Ganga River Crisis', size: 'w-48 h-36' },
        { top: '5%', right: '2%', rotate: '-8deg', delay: '1s', duration: '7s', img: 'https://images.unsplash.com/photo-1583475092727-5a07f10eee25?w=500&h=400&fit=crop&q=80', title: 'Yamuna: Delhi\'s Toxic River', size: 'w-48 h-36' },
        { top: '40%', left: '2%', rotate: '-6deg', delay: '2s', duration: '6.5s', img: 'https://images.unsplash.com/photo-1621451537084-482c73073a0f?w=500&h=400&fit=crop&q=80', title: 'Industrial Waste', size: 'w-48 h-36' },
        { top: '40%', right: '2%', rotate: '6deg', delay: '0.5s', duration: '7.5s', img: 'https://images.unsplash.com/photo-1605600659908-0ef719419d41?w=500&h=400&fit=crop&q=80', title: 'Plastic Pollution', size: 'w-48 h-36' },
        { bottom: '5%', left: '2%', rotate: '5deg', delay: '1.5s', duration: '6.8s', img: 'https://images.unsplash.com/photo-1530587191325-3db32d826c18?w=500&h=400&fit=crop&q=80', title: 'Sewage Emergency', size: 'w-48 h-36' },
        { bottom: '5%', right: '2%', rotate: '-5deg', delay: '2.5s', duration: '7.2s', img: 'https://images.unsplash.com/photo-1618477461853-cf6ed80faba5?w=500&h=400&fit=crop&q=80', title: 'Beach Pollution', size: 'w-48 h-36' }
      ];
  
      const displayedData = chartData.length > 0 && chartDomain.end > 0
      ? chartData.filter(item => {
          const itemIndex = fullPollutionData.findIndex(d => d.year === item.year);
          return itemIndex >= chartDomain.start && itemIndex <= chartDomain.end;
        })
      : (chartDomain.end > 0 
          ? fullPollutionData.slice(chartDomain.start, chartDomain.end + 1)
          : []);
  
      return (
        <div className="min-h-screen bg-gray-50">
        {console.log("✅ React App rendered successfully")}
          <div className="relative w-full h-64 sm:h-80 md:h-96 lg:h-[32rem] bg-gray-900 overflow-hidden">
            {pollutionImages.map((image, index) => (
              <div
                key={index}
                className={`absolute inset-0 transition-opacity duration-1000 ${index === currentSlide ? 'opacity-100' : 'opacity-0'}`}
                style={{ zIndex: index === currentSlide ? 10 : 1 }}
              >
                <div className="w-full h-full bg-cover bg-center bg-no-repeat" style={{ backgroundImage: `url(${image.url})` }}>
                  <div className="absolute inset-0 bg-gradient-to-t from-black via-black/50 to-transparent flex flex-col justify-end p-4 sm:p-6 md:p-8 lg:p-12">
                    <h3 className="text-white text-xl sm:text-2xl md:text-3xl lg:text-4xl font-bold mb-1 sm:mb-2 drop-shadow-lg">{image.title}</h3>
                    <p className="text-white text-sm sm:text-base md:text-lg lg:text-xl drop-shadow-lg">{image.description}</p>
                  </div>
                </div>
              </div>
            ))}
  
            <button onClick={prevSlide} className="absolute left-2 sm:left-4 top-1/2 transform -translate-y-1/2 bg-white/50 hover:bg-white/75 rounded-full p-1.5 sm:p-2 transition-smooth z-20" aria-label="Previous slide">
              <ChevronLeft className="w-4 h-4 sm:w-6 sm:h-6 text-gray-800" />
            </button>
            <button onClick={nextSlide} className="absolute right-2 sm:right-4 top-1/2 transform -translate-y-1/2 bg-white/50 hover:bg-white/75 rounded-full p-1.5 sm:p-2 transition-smooth z-20" aria-label="Next slide">
              <ChevronRight className="w-4 h-4 sm:w-6 sm:h-6 text-gray-800" />
            </button>
  
            <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2 z-20">
              {pollutionImages.map((_, index) => (
                <button key={index} onClick={() => setCurrentSlide(index)} className={`w-3 h-3 rounded-full transition-smooth ${index === currentSlide ? 'bg-white' : 'bg-white/50'}`} aria-label={`Go to slide ${index + 1}`} />
              ))}
            </div>
          </div>
  
          <div className="relative bg-gradient-to-br from-blue-600 to-blue-900 text-white py-16 sm:py-24 md:py-32 overflow-hidden">
            <div className="absolute inset-0 overflow-hidden opacity-20 pointer-events-none">
              {newspaperCutouts.map((item, i) => (
                <div key={i} className="absolute float-animate" style={{ top: item.top, left: item.left, right: item.right, bottom: item.bottom, animationDuration: item.duration, animationDelay: item.delay }}>
                  <div className={`${item.size} bg-white rounded-lg shadow-2xl overflow-hidden border-2 border-white`} style={{ transform: `rotate(${item.rotate})` }}>
                    <img src={item.img} alt={item.title} className="w-full h-full object-cover" />
                    <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent flex items-end p-2">
                      <p className="text-white text-xs font-bold">{item.title}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
  
            <div className="container mx-auto px-4 text-center relative z-10">
              <h1 className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold mb-4 sm:mb-6">Water Pollution</h1>
              <p className="text-xl sm:text-2xl md:text-3xl mb-6 sm:mb-8">A Silent Crisis</p>
              <p className="text-base sm:text-lg md:text-xl max-w-3xl mx-auto mb-8 sm:mb-12">
                Water is essential for life, but pollution is rapidly making clean water scarce. Join us in understanding and combating this critical environmental challenge.
              </p>
              <button onClick={() => setActiveSection('crisis')} className="bg-white text-blue-900 px-6 sm:px-8 py-3 sm:py-4 rounded-full text-base sm:text-lg font-semibold hover:bg-blue-100 transition-smooth shadow-lg hover:shadow-xl transform hover:scale-105 active:scale-95">
                Learn More
              </button>
            </div>
          </div>
  
          <div className="bg-gradient-to-br from-red-50 to-orange-50 py-8 sm:py-12 md:py-16">
            <div className="container mx-auto px-4">
              <h2 className="text-2xl sm:text-3xl md:text-4xl font-bold text-center mb-3 sm:mb-4 text-red-800">The Rising Crisis: Global Water Data (2000-2024)</h2>
              <p className="text-center text-gray-700 mb-6 sm:mb-8 text-sm sm:text-base md:text-lg max-w-3xl mx-auto px-4">
                Real-world data from WHO/UNICEF JMP showing the alarming trends in water pollution and health impacts over the past two decades.
              </p>
  
              <div className="bg-white p-4 sm:p-6 md:p-8 rounded-lg shadow-2xl max-w-6xl mx-auto">
                <div className="flex justify-between items-center mb-4 sm:mb-6">
                  <div className="flex gap-2">
                    <button onClick={handleZoomIn} disabled={zoomLevel >= 3} className="flex items-center gap-1 px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-smooth text-sm shadow-md active:scale-95">
                      <ZoomIn className="w-4 h-4" />
                      <span className="hidden sm:inline">Zoom In</span>
                    </button>
                    <button onClick={handleZoomOut} disabled={zoomLevel <= 1} className="flex items-center gap-1 px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-smooth text-sm shadow-md active:scale-95">
                      <ZoomOut className="w-4 h-4" />
                      <span className="hidden sm:inline">Zoom Out</span>
                    </button>
                    <button onClick={handleResetZoom} disabled={zoomLevel === 1} className="flex items-center gap-1 px-3 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-smooth text-sm shadow-md active:scale-95">
                      <Maximize2 className="w-4 h-4" />
                      <span className="hidden sm:inline">Reset</span>
                    </button>
                  </div>
                  <div className="text-xs sm:text-sm text-gray-600 font-medium">
                    Zoom: {zoomLevel.toFixed(1)}x
                  </div>
                </div>
  
                <div className="overflow-x-auto">
                  <ResponsiveContainer width="100%" height={300}>
                    <LineChart data={displayedData}>
                      <CartesianGrid strokeDasharray="3 3" stroke="#e0e0e0" />
                      <XAxis dataKey="year" stroke="#666" style={{ fontSize: '12px', fontWeight: 'bold' }} />
                      <YAxis yAxisId="left" stroke="#666" style={{ fontSize: '12px', fontWeight: 'bold' }} />
                      <YAxis yAxisId="right" orientation="right" stroke="#666" style={{ fontSize: '12px', fontWeight: 'bold' }} />
                      <Tooltip contentStyle={{ backgroundColor: '#fff', border: '2px solid #3b82f6', borderRadius: '8px', padding: '10px' }} formatter={(value, name) => {
                        if (name === 'People without Safe Water (%)') return `${value}%`;
                        if (name === 'Plastic to Ocean (Million tonnes)') return `${value}M tonnes`;
                        if (name === 'Diarrhea Deaths (thousands)') return `${value}k deaths`;
                        return value;
                      }} />
                      <Legend wrapperStyle={{ paddingTop: '20px', fontSize: '12px' }} iconType="line" />
                      <Line yAxisId="left" type="monotone" dataKey="unsafeWater" stroke="#ef4444" strokeWidth={zoomLevel > 1.5 ? 4 : 3} name="People without Safe Water (%)" dot={{ fill: '#ef4444', r: zoomLevel > 1.5 ? 4 : 3 }} activeDot={{ r: zoomLevel > 1.5 ? 8 : 6 }} animationDuration={300} />
                      <Line yAxisId="right" type="monotone" dataKey="plasticOcean" stroke="#3b82f6" strokeWidth={zoomLevel > 1.5 ? 4 : 3} name="Plastic to Ocean (Million tonnes)" dot={{ fill: '#3b82f6', r: zoomLevel > 1.5 ? 4 : 3 }} activeDot={{ r: zoomLevel > 1.5 ? 8 : 6 }} animationDuration={300} />
                      <Line yAxisId="left" type="monotone" dataKey="diarrheaDeaths" stroke="#f59e0b" strokeWidth={zoomLevel > 1.5 ? 4 : 3} name="Diarrhea Deaths (thousands)" dot={{ fill: '#f59e0b', r: zoomLevel > 1.5 ? 4 : 3 }} activeDot={{ r: zoomLevel > 1.5 ? 8 : 6 }} animationDuration={300} />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
  
                <div className="mt-6 sm:mt-8 grid sm:grid-cols-2 md:grid-cols-3 gap-3 sm:gap-4">
                  <div className="bg-red-50 p-4 rounded-lg border-l-4 border-red-500">
                    <h4 className="font-bold text-red-800 mb-2 flex items-center">
                      <div className="w-3 h-3 bg-red-500 rounded-full mr-2"></div>
                      Unsafe Water Access
                    </h4>
                    <p className="text-sm text-gray-700">
                      Despite progress, <strong>27% of the global population</strong> (2.2 billion people) still lack safely managed drinking water in 2022.
                    </p>
                    <p className="text-xs text-gray-600 mt-2">Source: WHO/UNICEF JMP 2022</p>
                  </div>
                  <div className="bg-blue-50 p-4 rounded-lg border-l-4 border-blue-500">
                    <h4 className="font-bold text-blue-800 mb-2 flex items-center">
                      <div className="w-3 h-3 bg-blue-500 rounded-full mr-2"></div>
                      Plastic Pollution Crisis
                    </h4>
                    <p className="text-sm text-gray-700">
                      Plastic waste entering oceans peaked at <strong>12 million tonnes/year</strong> (2019), with recent better waste management reducing it to 1.5M tonnes.
                    </p>
                    <p className="text-xs text-gray-600 mt-2">Source: Jambeck et al. 2015, OECD 2022</p>
                  </div>
                  <div className="bg-orange-50 p-4 rounded-lg border-l-4 border-orange-500">
                    <h4 className="font-bold text-orange-800 mb-2 flex items-center">
                      <div className="w-3 h-3 bg-orange-500 rounded-full mr-2"></div>
                      Health Impact
                    </h4>
                    <p className="text-sm text-gray-700">
                      Over <strong>1.1 million deaths annually</strong> from diarrheal diseases linked to unsafe water, sanitation, and hygiene (WASH).
                    </p>
                    <p className="text-xs text-gray-600 mt-2">Source: WHO Global Health Observatory 2019</p>
                  </div>
                </div>
  
                <div className="mt-6 bg-yellow-50 p-4 rounded-lg border-l-4 border-yellow-500">
                  <h4 className="font-bold text-yellow-800 mb-2">Additional Crisis Facts</h4>
                  <ul className="text-sm text-gray-700 space-y-1">
                    <li>• <strong>80% of global wastewater</strong> is released untreated into the environment (UN-Habitat)</li>
                    <li>• Progress in safe water access: 61% (2000) → 73% (2022), but 703 million still lack access</li>
                    <li>• Years 2023-2024 are estimates based on current trends</li>
                  </ul>
                </div>
              </div>
  
              <div className="text-center mt-6 sm:mt-8 px-4">
                <p className="text-base sm:text-lg md:text-xl font-semibold text-gray-800">
                  Data Animation Progress: Year {chartData.length > 0 ? chartData[chartData.length - 1].year : 2000}
                </p>
                <div className="w-full max-w-2xl mx-auto mt-3 sm:mt-4 bg-gray-200 rounded-full h-2 sm:h-3 overflow-hidden">
                  <div className="bg-gradient-to-r from-red-500 to-orange-500 h-full rounded-full transition-all duration-300" style={{ width: `${(animationProgress / fullPollutionData.length) * 100}%` }} />
                </div>
              </div>
            </div>
          </div>
  
          <div className="container mx-auto px-4 py-8 sm:py-12 md:py-16">
            <h2 className="text-2xl sm:text-3xl md:text-4xl font-bold text-center mb-8 sm:mb-12">Why This Matters</h2>
            <div className="grid sm:grid-cols-2 gap-6 sm:gap-8 max-w-4xl mx-auto">
              <div className="bg-white p-8 rounded-lg shadow-lg hover:shadow-xl transition-smooth">
                <Heart className="w-12 h-12 text-red-500 mb-4" />
                <h3 className="text-2xl font-bold mb-4">Basic Human Need</h3>
                <p className="text-gray-700">Water is fundamental to life, yet we often take it for granted. Rivers like the Ganga and Yamuna hold cultural significance but face severe pollution.</p>
              </div>
              <div className="bg-white p-8 rounded-lg shadow-lg hover:shadow-xl transition-smooth">
                <AlertTriangle className="w-12 h-12 text-yellow-500 mb-4" />
                <h3 className="text-2xl font-bold mb-4">Health Crisis</h3>
                <p className="text-gray-700">Waterborne diseases like cholera, typhoid, and diarrhea affect millions. Understanding pollution helps protect our health and future.</p>
              </div>
            </div>
          </div>
        </div>
      );
  });

export default HomePage;